
*,
*::after,
*::before {
  box-sizing: border-box;
}

.container {
  width: 33%;
  min-width: 390px;
  height: 680px;
  margin: 30px auto;
  padding: 0 10px;
}

.col-12 {
  height: 100%;
}

.row {
  height: 25%;
  padding-bottom: 20px;
}

.item {
  height: 100%;
}

.col-2 {
  padding: 0 5px;
}

.col-8{
  padding: 0;
}

.col-4{
  padding: 0;
}

.svg {
  height: 100%;
  width: 100%;
  overflow: visible;
}

.shaft {
  height: 50px;
}

